import Main from '@/components/common/Main.vue';

export const routes = [
    {
        path: '/',
        redirect: '/home/index'
    },
    {
        path: '/home',
        component: Main,
        meta: {
          title: '系统首页'
        },
        imgurl: './../assets/img/home.png',
        children: [{
          path: '/home/index',
          name: 'home',
          icon: 'el-icon-menu',
          imgurl: './../assets/img/home.png',
          component: resolve => require(['../components/page/home/index.vue'], resolve),
          meta: {
            title: '系统首页'
          }
        }]
    },
    {
        path: '/main',
        component: Main,
        meta: {
          title: '主后台'
        },
        icon: 'el-icon-menu',
        imgurl: './../assets/img/home.png',
        children: [
            {
                path: '/main/mediamanagement',
                name: 'mediamanagement',
                component: resolve => require(['../components/page/MainBackstage/MediaManagement.vue'], resolve),
                meta: {
                    title: '媒体管理'
                }
            },
            {
                path: '/main/datacenter',
                name: 'datacenter',
                component: resolve => require(['../components/page/MainBackstage/DataCenter.vue'], resolve),
                meta: {
                    title: '数据中心'
                }
            },
            {
                path: '/main/moneymanagement',
                name: 'moneymanagement',
                component: resolve => require(['../components/page/MainBackstage/MoneyManagement.vue'], resolve),
                meta: {
                    title: '总代金额管理'
                }
            },
            {
                path: '/main/createmedia',
                name: 'createmedia',
                component: resolve => require(['../components/page/MainBackstage/CreateMedia.vue'], resolve),
                meta: {
                    title: '创建媒体'
                },
                ishide: false
            }
        ]
    },

    {
        path: '/general',
        component: Main,
        meta: {
          title: '总代后台'
        },
        icon: 'el-icon-s-management',
        imgurl: './../assets/img/home.png',
        children: [
            {
                path: '/general/advertmanagement',
                name: 'advertmanagement',
                component: resolve => require(['../components/page/GeneralBackstage/AdvertManagement.vue'], resolve),
                meta: {
                    title: '客户广告管理'
                }
            },
            {
                path: '/general/advertposition',
                name: 'advertposition',
                component: resolve => require(['../components/page/GeneralBackstage/AdvertPosition.vue'], resolve),
                meta: {
                    title: '广告位管理'
                }
            },
            {
                path: '/general/customermanagement',
                name: 'customermanagement',
                component: resolve => require(['../components/page/GeneralBackstage/CustomerManagement.vue'], resolve),
                meta: {
                    title: '客户管理'
                }
            },
            {
                path: '/general/createcustomer',
                name: 'createcustomer',
                component: resolve => require(['../components/page/GeneralBackstage/CreateCustomer.vue'], resolve),
                meta: {
                    title: '创建客户'
                },
                ishide: false
            },
            {
                path: '/general/advertissued',
                name: 'advertissued',
                component: resolve => require(['../components/page/GeneralBackstage/AdvertIssued.vue'], resolve),
                meta: {
                    title: '广告下发'
                }
            },
            {
                path: '/general/createagent',
                name: 'createagent',
                component: resolve => require(['../components/page/GeneralBackstage/CreateAgent.vue'], resolve),
                meta: {
                    title: '创建代理'
                }
            },
            {
                path: '/general/datacenter',
                name: 'datacenter',
                component: resolve => require(['../components/page/GeneralBackstage/DataCenter.vue'], resolve),
                meta: {
                    title: '数据中心'
                }
            },
        ]
    },

    {
        path: '/client',
        component: Main,
        meta: {
          title: '客户后台'
        },
        icon: 'el-icon-s-grid',
        imgurl: './../assets/img/home.png',
        children: [
            {
                path: '/client/newadgroup',
                name: 'newadgroup',
                component: resolve => require(['../components/page/ClientBackstage/NewadGroup.vue'], resolve),
                meta: {
                    title: '新建广告组'
                },
                ishide: false
            },

            {
                path: '/client/createplan',
                name: 'createplan',
                component: resolve => require(['../components/page/ClientBackstage/CreatePlan.vue'], resolve),
                meta: {
                    title: '创建计划'
                },
                ishide: false
            },
            {
                path: '/client/creativecreation',
                name: 'creativecreation',
                component: resolve => require(['../components/page/ClientBackstage/CreativeCreation.vue'], resolve),
                meta: {
                    title: '创意创建'
                },
                ishide: false
            },
            {
                path: '/client/planlist',
                name: 'planlist',
                component: resolve => require(['../components/page/ClientBackstage/PlanList.vue'], resolve),
                meta: {
                    title: '计划列表'
                }
            },
            {
                path: '/client/advertgrouplist',
                name: 'advertgrouplist',
                component: resolve => require(['../components/page/ClientBackstage/AdvertgroupList.vue'], resolve),
                meta: {
                    title: '广告组列表'
                }
            },
            {
                path: '/client/datareport',
                name: 'datareport',
                component: resolve => require(['../components/page/ClientBackstage/DataReport.vue'], resolve),
                meta: {
                    title: '数据报表'
                }
            },
            {
                path: '/client/creativereport',
                name: 'creativereport',
                component: resolve => require(['../components/page/ClientBackstage/CreativeReport.vue'], resolve),
                meta: {
                    title: '创意报表'
                }
            },
            {
                path: '/client/financereport',
                name: 'financereport',
                component: resolve => require(['../components/page/ClientBackstage/FinanceReport.vue'], resolve),
                meta: {
                    title: '财务报表'
                }
            },
            {
                path: '/client/personalcenter',
                name: 'personalcenter',
                component: resolve => require(['../components/page/ClientBackstage/PersonalCenter.vue'], resolve),
                meta: {
                    title: '个人中心'
                }
            },
        ]
    },

    {
        path: '/agent',
        component: Main,
        meta: {
          title: '代理后台'
        },
        icon: 'el-icon-s-grid',
        imgurl: './../assets/img/home.png',
        children: [
            {
                path: '/agent/customermanagement',
                name: 'customermanagement',
                component: resolve => require(['../components/page/AgentBackstage/CustomerManagement.vue'], resolve),
                meta: {
                    title: '客户管理'
                }
            },
            {
                path: '/agent/datarecord',
                name: 'datarecord',
                component: resolve => require(['../components/page/AgentBackstage/DataRecord.vue'], resolve),
                meta: {
                    title: '数据记录'
                }
            },
            {
                path: '/agent/createcustomer',
                name: 'createcustomer',
                component: resolve => require(['../components/page/AgentBackstage/CreateCustomer.vue'], resolve),
                meta: {
                    title: '创建客户'
                },
                ishide: false
            }
        ]
    },

    {
        path: '/login',
        name: 'login',
        component: () => import("@/components/page/Login.vue")
    }
]